import { ServerPreview } from '@model/serverPreview'
import React, { FC, useState } from 'react'
import { AnimatePresence, motion } from 'framer-motion'
import s from './index.module.scss'
import { AnimationState } from '@features/lib/animationState'

const nameAnimation : AnimationState = {
    variants: {
        show: {
            opacity: 1,
            transform: "Translate(-50%, 0%)"
        },
        hidden: {
            opacity: 0,
            transform: "Translate(-50%, 0%)"
        }
    },
    initial: 'hidden',
    animate: 'show',
    exit: 'hidden'

}

export const ServersListCard : FC<ServerPreview> = ({ name, picUrl }) => {
    const [isShowName, setShowName] = useState<boolean>(false)

    return (
        <motion.div 
            onHoverStart={() => setShowName(true)}
            onHoverEnd={() => setShowName(false)}
            className={s.container}>
            <motion.img className={s.pic} src={picUrl} />
            <AnimatePresence>
                {
                    isShowName &&
                    <motion.div
                        variants={nameAnimation.variants}
                        initial={nameAnimation.initial}
                        animate={nameAnimation.animate}
                        exit={nameAnimation.exit}
                        className={s.name}>
                        { name }
                    </motion.div>
                }                
            </AnimatePresence>            
        </motion.div>
    )
}  