import React, { FC, useEffect, useRef, useState } from 'react'
import { motion, useScroll } from 'framer-motion'
import s from './index.module.scss'
import { useAppDispatch } from '@hooks/hooks'
import { PaginationResponse } from '@model/paginationResponce'
import { PicturedEntity } from '@model/Entity'
import { ActionCreatorWithPayload } from '@reduxjs/toolkit'
import { BaseQueryFn, FetchArgs, FetchBaseQueryError, FetchBaseQueryMeta, QueryDefinition } from '@reduxjs/toolkit/dist/query'
import { PaginationRequest } from '@model/paginationRequest'
import { UseQueryHookResult } from '@reduxjs/toolkit/dist/query/react/buildHooks'

interface SectionListProps<T extends PicturedEntity>
{
    fetchData: UseQueryHookResult<QueryDefinition<PaginationRequest, BaseQueryFn<string | FetchArgs, unknown, FetchBaseQueryError, {}, FetchBaseQueryMeta>, never, PaginationResponse<T>, string>, any>,
    entts: T[],
    setReducer: ActionCreatorWithPayload<any, string>,
    ListItem: (entt : PicturedEntity) => JSX.Element
}

const variants = {
    open: {
        transition: { staggerChildren: 0.07, delayChildren: 0.2 }
    },
    closed: {
        transition: { staggerChildren: 0.05, staggerDirection: -1 }
    }
}

const itemVariants = {
    open: {
      y: 0,
      opacity: 1,
      transition: {
        x: { stiffness: 1000, velocity: -100 }
      }
    },
    closed: {
      y: 50,
      opacity: 0,
      transition: {
        x: { stiffness: 1000 }
      }
    }
  };


export const SectionList = <T extends PicturedEntity>({fetchData, entts,  setReducer, ListItem} : SectionListProps<T>) => 
{
    const [mounted, setMounted] = useState<boolean>(false)
    const [page, setPage] = useState<number>(1)     
    const dispatch = useAppDispatch()
    const listRef = useRef(null)
    const { scrollYProgress } = useScroll({ container: listRef })
    const { data, error, refetch, isLoading } = fetchData

    useEffect(() => {
        setMounted(true)
    }, [])

    useEffect(() => {
        if (mounted && scrollYProgress.get() === 1) {
            setPage(page + 1)
            refetch()
            data && dispatch(setReducer(data))
        }
    }, [scrollYProgress.get()])

    return (
            <motion.div
                animate="open"
                exit="closed" 
                variants={variants}
                ref={listRef}
                className={s.list} >
                { 
                    entts.map(entt => (
                        <motion.div
                            animate="open"
                            exit="closed"
                            variants={itemVariants}
                            key={entt.id}
                            className={s.item}>
                            {ListItem(entt)}
                        </motion.div>
                    ))
                } 
            </motion.div>        
    )
}