import { Token } from '@model/authToken'
import { User } from '@model/user'
import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/dist/query/react'
import { SignInFormData } from '@ui/blocks/sign-in-form'
import { currentApi } from './api'

interface SignInRequest {
    form : SignInFormData
}

export interface SignInResponse {
    user: User,
    token: Token
}

export const AuthApi = createApi({
    reducerPath: 'AuthApi',
    baseQuery: fetchBaseQuery({
        baseUrl: currentApi
    }),
    tagTypes: ['User'],
    endpoints: (build) => ({
        signIn: build.mutation<SignInResponse, SignInRequest>({
            query: (form : SignInRequest) => ({
                url: '/signIn',
                method: 'POST',
                body: form,                
            }),
            //invalidateTags: ['User'],            
        }),
        signOut: build.mutation<void, Token>({
            query: (token : Token) => ({
                url: '/signIn',
                method: 'DELETE',
                headers: {
                    Authorization: token
                }
            }),
            //invalidateTags: ['User'],
            
        })
    })
})