import React, { FC, useLayoutEffect } from 'react'
import { AnimatePresence, motion } from 'framer-motion'
import s from './index.module.scss'
import { AppName } from '@ui/blocks/app-name'
import { AppFooter } from '@ui/blocks/app-footer'
import notes from '@assets/notes.svg'
import micro from '@assets/microphone.svg'
import woman from '@assets/woman.svg'
import man from '@assets/man.svg'
import { Outlet } from 'react-router-dom'

export const AuthPageTemplate = () => {
    return (
        <motion.div className={s.authPage}>
            <motion.div className={ s.appNameBackground }>
                <motion.div className={s.appName}>
                    <motion.div 
                        layout                            
                        className={s.notesWrapper}>
                        <motion.img  
                            layout
                            animate={{ 
                                transform: ["translateX(0%)", "translateX(100%)"]
                            }}
                            transition={{ 
                                times: [0, 1],
                                repeat: Infinity,
                                duration: 20,
                                ease: 'linear'
                            }}                  
                            src={notes} className={s.notes} />
                        <motion.img 
                            layout
                            animate={{ 
                                transform: ["translateX(-100%)", "translateX(0%)"]
                            }}
                            transition={{ 
                                times: [0, 1],
                                repeat: Infinity,
                                duration: 20,
                                ease: 'linear'
                            }}                  
                            src={notes} className={s.notes} />
                        <motion.img src={notes} className={s.fakeNotes}/>
                    </motion.div>                        
                    <AppName />
                </motion.div>                    
            </motion.div> 
            <motion.div className={s.contentContainer}>
                <motion.div                         
                    className={s.manContainer}>
                    <motion.img src={man} className={s.person} />
                    <motion.img src={micro} className={s.micro} />
                </motion.div>

                <motion.div className={s.conent}>
                    <Outlet/>
                    <motion.div className={s.authPageFooter}>
                        <AppFooter/>
                    </motion.div>
                </motion.div>         
                
                <motion.div                    
                    className={s.womanContainer}>
                    <motion.img src={micro} className={s.micro} /> 
                    <motion.img src={woman} className={s.person} />
                </motion.div>
                
            </motion.div>                
        </motion.div>
    )
}