import React, { FC, useState } from 'react'
import { motion } from 'framer-motion'
import s from './index.module.scss'

interface UserPanelButtonProps {
    iconSrc: string
}

export const UserPanelButton : FC<UserPanelButtonProps> = ({iconSrc}) => {
    const [active, setActive] = useState()

    return (
        <motion.button
            layout
            className={s.button}>
            <motion.img
                whileTap={{ scale: 0.88 }}          
                className={s.icon}
                src={iconSrc}/>
        </motion.button>
        
    )
}