import { PaginationRequest } from '@model/paginationRequest'
import { PaginationResponse } from '@model/paginationResponce'
import { ServerPreview } from '@model/serverPreview'
import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/dist/query/react'
import { currentApi } from './api'

export const ServersApi = createApi({
    reducerPath: 'ServersApi',
    baseQuery: fetchBaseQuery({
        baseUrl: currentApi
    }),
    endpoints: (build) => ({
        getServers: build.query<PaginationResponse<ServerPreview>, PaginationRequest>({
            query: (request) => ({
                url: '/servers',
                method: 'GET',
                params: request       
            })            
        })
    })
})