import React, { FC } from 'react'
import { NavLink } from 'react-router-dom'
import { Control, Controller } from 'react-hook-form'
import { motion } from 'framer-motion'
import s from './index.module.scss'
import { AuthFormButton } from '@ui/elements/auth-form-button'
import { AuthFormInput } from '@ui/elements/auth-form-input'
import { AnimationState } from '@features/lib/animationState'

export interface controllerData {
    name: string,
    placeholder: string,
    type?: string
}

interface AuthFormProps {
    controllers: controllerData[],
    control: Control<any, any>,
    button: {
        text: string
    },
    link: {
        text: string,
        to: string
    },
    onSubmit: React.FormEventHandler
}

const containerState : AnimationState = {
    variants: {
        hidden: { 
            opacity: 0,        
        },
        show: {
            opacity: 1,
            transition: {
            staggerChildren: 0.25
            }
        },
        exit: {
            transform: "translateY(50%)"
        }
    },
    initial: 'hidden',
    animate: 'show',
    exit: 'exit'
}

const itemState : AnimationState = {
    variants: {
        hidden: { 
            opacity: 0,
            transform: "translateY(50%)"
        },
        show: { 
            opacity: 1,
            transform: "translateY(0%)"
        }
    },
    initial: 'hidden',
    animate: 'show',
}

export const AuthForm : FC<AuthFormProps> = ({controllers, button, link, control, onSubmit}) => {
    return (   
        <motion.form
            key='auth-form'
            variants={containerState.variants}
            initial={containerState.initial}
            animate={containerState.animate}
            exit={containerState.exit}
            className={s.authForm} 
            onSubmit={onSubmit}>
                        
            {controllers.map(controller => 
                <Controller
                    key={controller.name}
                    name={controller.name}
                    control={control}
                    render={({field}) => <AuthFormInput
                            placeholder={controller.placeholder}
                            animationState={itemState}                                            
                            type={controller.type && controller.type}
                            {...field}/>}
                />)}
            
            <motion.div
                className={s.formController}
                variants={itemState.variants}
            >
                <AuthFormButton>
                    {button.text}
                </AuthFormButton>
                <NavLink to={link.to}>
                    {link.text}
                </NavLink>                
            </motion.div>           
        
        </motion.form>
    )
}


