import {
    createRoutesFromElements,
    createBrowserRouter,
    Route,  
    Outlet
} from 'react-router-dom'
import { ErrorPage } from './error'
import { AppTemplate } from './templates/app-template'
import { AuthPageTemplate } from './templates/auth-template'
import { DefaultTemplate } from './templates/default-template'

import { SignInForm } from '@ui/blocks/sign-in-form'
import { SignUpForm } from '@ui/blocks/sign-up-form'
import { ProtectedRoute } from '@features/auth/protectedRoute'
import { MainPage } from './main'


export const router = createBrowserRouter(
    createRoutesFromElements(
        <Route
            path=''
            element={<DefaultTemplate/>}
            errorElement={<ErrorPage/>}>
                {/* Settings */}
                <Route path='/settings' element={
                    <ProtectedRoute>
                        <div>
                            Settings
                            <Outlet/>
                        </div>
                    </ProtectedRoute> }>

                        <Route path='/settings/profile' element={<div>Profile</div>}/>
                </Route>
                {/* Main Page */}
                <Route
                    path='/'
                    element={
                        <ProtectedRoute>
                            <AppTemplate/>
                        </ProtectedRoute>}>
                        <Route
                            path=''
                            element={<MainPage />}>                        
                        </Route>                 
                </Route>
            
                {/* Authentication */}
                <Route
                    path=''
                    element={<AuthPageTemplate/>}>
                        <Route
                            path='sign-in'
                            element={<SignInForm/>}/>
                        <Route
                            path='sign-up'
                            element={<SignUpForm/>}/>                    
                </Route>            
        </Route>
    )
)
