import React, { FC, useEffect } from 'react'
import { useForm } from 'react-hook-form'
import { AuthForm, controllerData } from '@ui/elements/auth-form'
import { AuthApi, SignInResponse } from '@services/AuthService'
import { useAppDispatch } from '@hooks/hooks'
import { useNavigate, useLocation } from 'react-router-dom'
import { authSlice } from '@features/auth/AuthSlice'

export interface SignInFormData {
    username: string,
    password: string,
}

const controllers : controllerData[] = [
    {
        name: "username",
        placeholder: 'Username:'
    },
    {
        name: "password",
        placeholder: 'Password:',
        type: "password"
    },
]

export const SignInForm : FC<any> = () => {
    const [fetchSignIn, result] = AuthApi.useSignInMutation()
    const { control, handleSubmit, } = useForm<SignInFormData>()
    const { signIn } = authSlice.actions
    const dispatch = useAppDispatch()
    const navigate = useNavigate()
    const location = useLocation()

    const onSubmit = handleSubmit(async (data) => {
        await fetchSignIn({form: data})

        if (result.data) {
            dispatch(signIn(result.data))
            let origin = location.state?.from?.pathName
            if (!origin || origin === '/sign-up') origin = '/'
            navigate(origin)
        }
    })

    const fakeSubmit = () => {
        dispatch(signIn({
            token: 'Barer 687rh3fgh3476rt2gd21',
            user: {
                id: 1,
                name: 'Altairka',
                picUrl: 'https://sun9-71.userapi.com/impg/QANf1Fm7KwAzauASKrZYT9dSI9M25d9ZcP9GiQ/oDK9pXNxGMI.jpg?size=1043x1043&quality=96&sign=05374274bc67b7ef09e6a0941044f03f&type=album'
            }
        }))
        let origin = location.state?.from?.pathname
        console.log(origin)
        if (!origin || origin === '/sign-up') origin = '/'
        navigate(origin)
    }
    

    return (
        <AuthForm
            controllers={controllers}
            button={{
                text: 'Sign In'
            }}
            link={{
                text: 'No account? Sign Up',
                to: '/sign-up'
            }}
            control={control}
            onSubmit={fakeSubmit}      
        /> 
    )
}
