import { useState } from 'react'
import {  useAppSelector } from '@hooks/hooks'
import { friendsSlice } from '@features/friends/friendsSclice'
import { SectionList } from '@ui/elements/section-list'
import { ServerPreview } from '@model/serverPreview'
import { PicturedEntity } from '@model/Entity'
import { ServersListCard } from '@ui/elements/server-list-card'
import { ServersApi } from '@services/ServersService'

export const ServersList = () => {
    const pageCount = 8
    const [page, setPage] = useState(1) 
    const fetchData = ServersApi.useGetServersQuery({page: page, count: pageCount})
    const { servers } = useAppSelector(state => state.serversReducer)
    const { setFriends } = friendsSlice.actions

    return <SectionList<ServerPreview> 
            entts={servers}
            fetchData={fetchData}
            setReducer={setFriends}
            ListItem={(entt : PicturedEntity) => <ServersListCard id={entt.id} name={entt.name} picUrl={entt.picUrl} />}
        />
}