import React from 'react'
import s from './index.module.scss'
import { motion } from 'framer-motion'
import { NavLink } from 'react-router-dom'
import blindGirl from '@assets/blind-girl.svg'
import blindGuy from '@assets/blind-guy.svg'

export const BlindTalksBanner = () => {
    return (
        <motion.div className={s.container}>
            <NavLink to='/blind-talks'>
                <motion.div className={s.text}>
                    <motion.div className={s.header}>
                        Blind Talks
                    </motion.div>
                    <motion.div className={s.description}>
                        <motion.p>single or </motion.p>
                        <motion.p>with friends</motion.p>
                    </motion.div>
                </motion.div>
                <motion.img className={s.girl} src={blindGirl}/>
                <motion.img className={s.guy} src={blindGuy}/>
            </NavLink>            
        </motion.div>
    )
} 