import { Token } from '@model/authToken'
import { User } from '@model/user'
import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { SignInResponse } from '@services/AuthService'

interface AuthState {
    user: User,
    token: Token
}

const initialState : AuthState = {
    token: '',
    user: {
        id: 0,
        name: '',
        picUrl: ''
    }
}

const fakeState : AuthState = {
    token: 'Barer 687rh3fgh3476rt2gd21',
    user: {
        id: 1,
        name: 'Altairka',
        picUrl: 'https://sun9-71.userapi.com/impg/QANf1Fm7KwAzauASKrZYT9dSI9M25d9ZcP9GiQ/oDK9pXNxGMI.jpg?size=1043x1043&quality=96&sign=05374274bc67b7ef09e6a0941044f03f&type=album'
    }
}

export const authSlice = createSlice({
    name: 'auth',
    initialState: fakeState,
    reducers: {
        signIn(state, {payload}: PayloadAction<SignInResponse>) {
            state.token = payload.token 
            state.user = payload.user
        }
    }
})

export default authSlice.reducer