import { PaginationResponse } from '@model/paginationResponce'
import { ServerPreview } from '@model/serverPreview'
import { createSlice, PayloadAction } from '@reduxjs/toolkit'

interface ServersState {
    servers: ServerPreview[],
}

const initialState : ServersState = {
    servers: []
}

const fakeState : ServersState = {
    servers: [
        {
            id: 1,
            name: 'G.Party',
            picUrl: 'https://sun9-52.userapi.com/impg/ynpbAqbP5b-KEFlRsmTfpXt4HSFt3lgMHFkOBA/KPowFGqTQfw.jpg?size=796x448&quality=96&sign=41df09fd8d80bb6aaa016c34ca05bd58&type=album'
        },
        {
            id: 2,
            name: 'Enclave',
            picUrl: 'https://sun1-47.userapi.com/impg/ksg2EhsZOrxgne3xB58H-3XSE4AaRd6LrQcQMg/LJc7j5_VNO4.jpg?size=796x448&quality=96&sign=4e360c3f21e96c19b1992626aec8113e&type=album'
        },
        {
            id: 3,
            name: 'Midjourney',
            picUrl: 'https://sun9-68.userapi.com/impg/WQDqJH4Gp-S_KmD6PfNasVNGa64VM8VWFiaJ8g/Whsad0hlcRQ.jpg?size=796x448&quality=96&sign=e8e68216c60c4f5d5beaa77371466a1b&type=album'
        },
        {
            id: 4,
            name: 'InfinityRealm',
            picUrl: 'https://sun9-61.userapi.com/impg/lxhnvvrZc5yGr_AhA9FycMWKN0vTLXE9JZTMPg/O2hc9zahEr4.jpg?size=796x448&quality=96&sign=00999db8db4dcac1ee76d1d3b969972c&type=album'
        },        
    ]
}

export const serversSlice = createSlice({
    name: 'servers',
    initialState: fakeState,
    reducers: {
        setServers(state, {payload}: PayloadAction<PaginationResponse<ServerPreview>>) {
            state.servers = [...state.servers, ...payload.elements] 
        }
    }
})

export default serversSlice.reducer