import { configureStore, ThunkAction, Action, combineReducers } from '@reduxjs/toolkit'
import authReducer from '@features/auth/AuthSlice'
import friendsReducer from '@features/friends/friendsSclice'
import serversReducer from '@features/servers/serversSlice'
import { AuthApi } from '@services/AuthService'
import { FriendsApi } from '@services/FriendsService'
import { ServersApi } from '@services/ServersService'

const rootReducer = combineReducers({
  authReducer,
  [AuthApi.reducerPath]: AuthApi.reducer,
  friendsReducer,
  [FriendsApi.reducerPath]: FriendsApi.reducer,
  serversReducer,
  [ServersApi.reducerPath]: ServersApi.reducer,
})

export const store = configureStore({
  reducer: rootReducer,
  middleware: (getDefaultMiddleware) => getDefaultMiddleware()
    .concat(AuthApi.middleware)
    .concat(FriendsApi.middleware)
    .concat(ServersApi.middleware)
});

export type AppDispatch = typeof store.dispatch
export type RootState = ReturnType<typeof store.getState>
export type AppThunk<ReturnType = void> = ThunkAction<
  ReturnType,
  RootState,
  unknown,
  Action<string>
>
