import React from 'react'
import { motion } from 'framer-motion'
import s from './index.module.scss'
import { FriendsSection } from '@ui/blocks/friends-section'
import { ServersSection } from '@ui/blocks/servers-section'
import { ChatSection } from '@ui/blocks/chat-section'

export const MainPage = () => {
    return (
        <motion.div className={s.content}>
            <FriendsSection />
            <ChatSection />
            <ServersSection />
        </motion.div>
    )
}