import { FC, useState, useEffect, Fragment } from 'react'
import { NavLink } from 'react-router-dom'
import { AnimatePresence, motion } from 'framer-motion'
import s from './index.module.scss'

export interface link {
    name: string,
    to: string,
}

const blankLink : link = {
    name: '',
    to: ''
}

interface NameProps {
    name: string
}

const Name : FC<NameProps> = ({name}) => {
    const transition = (i: number) => ({
        duration: 0.15,
        delay: 0.1 * i,
        ease: 'linear',
    })

    const item = {
        hidden: {
            opacity: 0,
            transform: `translateY(-25%)`
        },
        shown: {
            opacity: 1,
            transform: `translateY(0%)`
        }
    }

    return (
        <motion.div layout key="letters">
            {
                name.split('').map((letter, i) => (
                    <motion.span
                        className={s.letter}
                        layout
                        key={i}
                        variants={item}
                        initial="hidden"
                        animate="shown"
                        exit="hidden"
                        transition={transition(i)}
                    >
                        {letter}
                    </motion.span>
                ))
            }            
        </motion.div>        
    )
}

export const Navigation = () => {
    const [backLink, setBackLink] = useState(blankLink)

    return (        
            <motion.nav className={s.navigation}>
                <AnimatePresence initial={false}>
                    <Name key="name" name={backLink == blankLink ? "CONVERSAY" : backLink.name} />
                </AnimatePresence>
                        
            </motion.nav>        
    )
}
