import React from 'react'
import { motion } from 'framer-motion'
import s from './index.module.scss'

export const AppFooter = () => {
    return (
        <motion.footer className={s.footer}>
            <motion.p className={s.corp}>
                Ravage Corporation
            </motion.p>
            <motion.p className={s.rights}>
                all right reserved
            </motion.p>
        </motion.footer> 
    )
}