import React, { FC } from 'react'
import { useForm } from 'react-hook-form'
import { AuthForm, controllerData } from '@ui/elements/auth-form'

export interface SignUpFormData {
    username: string,
    email: string,
    password: string,
}

const controllers : controllerData[] = [
    {
        name: "username",
        placeholder: 'Username:',
    },
    {
        name: "email",
        placeholder: 'Email:'
    },
    {
        name: "password",
        placeholder: 'Password:',
        type: "password"
    },
]

export const SignUpForm : FC<any> = () => {
    const { control, setValue, handleSubmit, formState: { errors } } = useForm<SignUpFormData>();
    const onSubmit = handleSubmit((data) => console.log(data));

    return (
        <AuthForm
            controllers={controllers}
            button={{
                text: 'Sign Up'
            }}
            link={{
                text: 'Have account? Sign In',
                to: '/sign-in'
            }}
            control={control}
            onSubmit={onSubmit}  
        /> 
    )
}