import React, { FC } from 'react'
import { motion, Variants } from 'framer-motion'
import s from './index.module.scss'
import { ControllerRenderProps } from 'react-hook-form'
import { AnimationState } from '@features/lib/animationState'

interface AuthFormInputProps {
    name: string,
    placeholder: string,
    animationState: AnimationState,
    type?: string | "text",    
    other?: ControllerRenderProps<any>
}

export const AuthFormInput : FC<AuthFormInputProps> = ({placeholder, type, animationState, ...other}) => {
    return (
        <motion.input
            key={placeholder}
            autoComplete='off'
            variants={animationState.variants}  
            className={s.authInput}
            type={type}         
            placeholder={placeholder}
            {...other}
        />
    )
}