import React, { useState } from 'react'
import { FriendsApi } from '@services/FriendsService'
import { useAppSelector } from '@hooks/hooks'
import { FriendListCard } from '@ui/elements/friend-list-card'
import { friendsSlice } from '@features/friends/friendsSclice'
import { SectionList } from '@ui/elements/section-list'
import { User } from '@model/user'
import { PicturedEntity } from '@model/Entity'

export const FriendsList = () => {
    const pageCount = 10
    const [page, setPage] = useState(1) 
    const fetchData = FriendsApi.useGetFriendsQuery({page: page, count: pageCount})
    const { friends } = useAppSelector(state => state.friendsReducer)
    const { setFriends } = friendsSlice.actions

    return <SectionList<User> 
            entts={friends}
            fetchData={fetchData}
            setReducer={setFriends}
            ListItem={(entt : PicturedEntity) => <FriendListCard id={entt.id} name={entt.name} picUrl={entt.picUrl} />}
        />
}