export const friendsSlice = createSlice({
    name: 'friends',
    initialState: fakeState,
    reducers: {
        setFriends(state, {payload}: PayloadAction<PaginationResponse<User>>) {
            state.friends = [...state.friends, ...payload.elements] 
        }
    }
})

export default friendsSlice.reducer