import React, { useState } from 'react'
import { motion } from 'framer-motion'
import s from './index.module.scss'
import { Section } from '@ui/elements/main-section'
import { FriendsList } from '@ui/blocks/friends-list'
import { BlindTalksBanner } from '@ui/blocks/blind-talks-banner'
import { SectionLabel, SectionName } from '@ui/elements/section-label'
import { MenuToggle } from '@ui/elements/menu-toggle'

const FriendsMenu = () => {
    const [isOpen, setOpen] = useState<boolean>(false)

    return (
        <SectionLabel>
            <MenuToggle isOpen={isOpen} toggle={() => setOpen(!isOpen)}/>
            <SectionName>
                Friends
            </SectionName>
        </SectionLabel>
    )
}

export const FriendsSection = () => {
    return (
        <Section>
            <FriendsMenu />
            <BlindTalksBanner/>
            <SectionLabel>
                <SectionName>
                    Offers
                </SectionName>
            </SectionLabel>
            <FriendsList />
        </Section>
    )
}