import React from 'react'
import { motion } from 'framer-motion'
import s from './index.module.scss'
import { useAppSelector } from '@hooks/hooks'
import { UserPanelButton } from '@ui/elements/userPanelButton'
import microSvg from '@assets/icons/micro.svg'
import headsetSvg from '@assets/icons/headset.svg'
import settingsSvg from '@assets/icons/settings.svg'
import { NavLink } from 'react-router-dom'

export const UserPanel = () => {
    const { user } = useAppSelector(state => state.authReducer)

    return (
        <motion.div className={s.container}>
            <motion.div className={s.leftControls}>
                <NavLink to='/settings/profile'>
                    <motion.div className={s.profile}>
                        <motion.img
                            src = {user.picUrl}
                            className={s.userPicture}/>
                        <motion.div
                            className={s.username}>
                            {user.name}
                        </motion.div>
                    </motion.div>
                </NavLink>                    
                <motion.div className={s.buttons}>
                    <UserPanelButton key={microSvg} iconSrc={microSvg}/>
                    <UserPanelButton key={headsetSvg} iconSrc={headsetSvg}/>                    
                </motion.div>
            </motion.div>
            <UserPanelButton key={settingsSvg} iconSrc={settingsSvg}/>
        </motion.div>
    ) 
}